import React,{useState} from 'react';
import { NavLink } from "react-router-dom";
import './Zalogowany.css'

const Zalogowany=()=>{
    const [text, setText]=useState('')
    const [send, setSend]=useState(false)
    
    const handlerText=(e)=>{
        setText(e.target.value)
    }
    const handlerClick=()=>{
        setText('')
        setSend(!send)
        
    };
    
    return(
        <div id='zal'>
            <button id='navlink'>
            <NavLink to='/'>wyloguj</NavLink>
            </button><br/>
            
            <textarea 
             placeholder='Wpisz text wiadomości'
              cols={30} rows={5}
               onChange={handlerText} 
               value={text}
                   /><br/>
            <button onClick={handlerClick}>Wyślij wiadomość</button><br/>
            <h3>{send?'Wiadomośc wysłana':''}</h3>
        </div>
    )
}
export default Zalogowany