import React from "react";
import { Route, Switch } from "react-router-dom";
import Logowanie from './Logowanie'
import Zalogowany from './Zalogowany'
const ErrorPage = () => (
  <h1 style={{ textAlign: "center" }}>
    Stron nie istnieje
    <br /> przejdz na:
    <br />
    <a href="https://pawwel3.github.io/cv-dobrowolski-pawel/">Home</a>
  </h1>
);
const Page = () => {
  return (
    <div>
      <Switch>
        <Route path="/" exact component={Logowanie} />
        <Route path="/zalogowany" component={Zalogowany} />
        <Route component={ErrorPage} />
      </Switch>
    </div>
  );
};

export default Page;
