import React, { Component } from 'react';
import { NavLink } from "react-router-dom";

class Logowanie extends Component{
    state={
        userEmail:'',
        password:'',
        accept:false,
        sprawdz:false,
       
        
    
        errors:{
          userEmail:false,
          password:false,
          accept:false
        }
      }
      message={
        userEmail_incorrect:'Brak @ w emailu',
        password_incorrect:'Hasło musi mieć 8 znaków',
        accept_incorrect:'Nie potwierdzona zgoda',

      }
      
     
	

      handlerChange=(e)=>{
        const name=e.target.name
        const type= e.target.type
        if(type==='text'|| type==='email'){
          this.setState({
            [name]:e.target.value
            
            })
        }else{
          this.setState({
            accept:e.target.checked
          })
          
        }
       
        
      }
      handelSubmit=(e)=>{
        e.preventDefault()
    
        const validation=this.formValidation()
        console.log(validation)
        if(validation.sprawdz){
            this.setState({
                sprawdz:true
            })
        }
         if(validation.correct){
            alert('poprawne email i hasło to zadanie2@pd.pl i zadanie2')
           this.setState({
            userEmail:'',
            password:'',
            accept:false,
            sprawdz:true,
            
        
            errors:{
              userEmail:false,
              password:false,
              accept:false
            }

          })
         }else{
           this.setState({
             errors:{
                userEmail:!validation.userEmail,
                password:!validation.password,
                accept:!validation.accept
                
             }
             
           })
         }
        
      }
      formValidation=()=>{
        let userEmail=false;
        let password=false;
        let accept=false;
        let correct=false;
       

    
        if(this.state.userEmail.indexOf('@')!==-1){
          userEmail=true
        }
        if(this.state.password.length>7){
          password=true
        }
        if(this.state.accept){
          accept=true
        }
       
        
        if(userEmail&&password&&accept){
            correct=true
          }
          this.setState({
            sprawdz:true
        })
        return({
          correct,
          accept,
          password,
          userEmail,
          
        })
      }
      componentWillUnmount(){
         
      }
     
      render(){
        
        return (
          <div >
            <h2>Logowanie</h2>
           <form onSubmit={this.handelSubmit} noValidate>
             <div>
             <label htmlFor='email'>Twój email 
             <input type='email' id='email' name='userEmail' 
             value={this.state.userEmail} onChange={this.handlerChange}/></label>
              {this.state.errors.userEmail&&<span>{this.message.userEmail_incorrect}</span>}
             </div>
             
          <div>
          <label htmlFor='text'>Twoje hasło 
             <input type='text' id='password' name='password' 
             value={this.state.pass} onChange={this.handlerChange}/></label>
             {this.state.errors.password&&<span>{this.message.password_incorrect}</span>}
          </div>
             
             <div>
             <label htmlFor='accept'>
              <input type='checkbox' id='accept' name='accept' checked={this.state.accept}
              onChange={this.handlerChange} />Wyrażam zgodę wszelaką
            </label>
            {this.state.errors.accept&&<span>{this.message.accept_incorrect}</span>}
             </div>
            
           <button>{this.state.accept&&this.state.password==='zadanie2'&&this.state.userEmail==='zadanie2@pd.pl'?<NavLink to='/zalogowany' >
        zaloguj
      </NavLink>:'zaloguj'}</button>
        <p>{this.state.sprawdz?'Błędny email lub hasło':''}</p>
           
           </form>
           
          </div>
        );
        }
      
    }
export default Logowanie